import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import 'hammerjs';
import { NgxCarouselModule } from 'ngx-carousel';

import { RouterModule, Routes } from '@angular/router';
import { ParallaxModule, ParallaxConfig } from 'ngx-parallax';

import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MatGridListModule, MatCardModule, MatButtonModule } from '@angular/material';

import { TextFormatPipe } from './pipes/text-format.pipe';
import { BackgroundUrlPipe } from './pipes/background-url.pipe';
import { JoinStylePipe } from './pipes/join-style.pipe';

import { LayoutService } from './services/layout.service';

import { AppComponent } from './app.component';
import { HomePageComponent } from './pages/home-page/home-page.component';
import { WorksPageComponent } from './pages/works-page/works-page.component';
import { WorkPageComponent } from './pages/work-page/work-page.component';
import { NavigationComponent } from './components/navigation/navigation.component';
import { NotFoundPageComponent } from './pages/not-found-page/not-found-page.component';
import { AvatarComponent } from './components/avatar/avatar.component';
import { WorkCardComponent } from './components/work-card/work-card.component';

const appRoutes: Routes = [{
	path: 'home',
	component: HomePageComponent
}, {
	path: 'works',
	component: WorksPageComponent
}, {
	path: 'works/:id',
	component: WorkPageComponent
}, {
	path: '**',
	component: NotFoundPageComponent
}];

@NgModule({
	declarations: [
		AppComponent,
		TextFormatPipe,
		BackgroundUrlPipe,
		JoinStylePipe,
		HomePageComponent,
		WorksPageComponent,
		WorkPageComponent,
		NavigationComponent,
		NotFoundPageComponent,
		AvatarComponent,
		WorkCardComponent
	],
	imports: [
		BrowserModule,
		NgxCarouselModule,
		RouterModule.forRoot(appRoutes, {
		// enableTracing: true
		}),
		ParallaxModule,
		BrowserAnimationsModule,
		MatGridListModule,
		MatCardModule,
		MatButtonModule
	],
	providers: [
		LayoutService
	],
	bootstrap: [
		AppComponent
	]
})
export class AppModule { }



