import { Component, OnInit } from '@angular/core';
import * as L from '../../resources/language';
import { LayoutService } from '../../services/layout.service';

@Component({
	selector: 'app-navigation',
	templateUrl: './navigation.component.html',
	styleUrls: ['./navigation.component.scss']
})
export class NavigationComponent implements OnInit {

	private navi: Array<{
		name: string,
		link: string
	}>;

	private owner: {
		name: string,
		avatar: string
	};

	private page: {
		css: string,
		avatarSize: number
	};

	private language: any;

	constructor(private layoutService: LayoutService) {
		this.language = L.Language.en;
		this.navi = [{
			name: this.language.home,
			link: "/home"
		}, {
			name: this.language.works,
			link: "/works"
		}];
		this.owner = {
			name: this.language.cathy,
			avatar: "/assets/avatar.jpg"
		};
		this.page = {
			css: "",
			avatarSize: 4
		};
	}

	ngOnInit() {
		this.drawLayout();
	}

	private drawLayout = function() {
		this.layoutService.dimension((width, height) => {
			// console.log(width, height);
			if(width < 479) {
				this.page.css = "sm";
				this.page.avatarSize = 3;
			} else {
				this.page.css = "";
				this.page.avatarSize = 4;
			}
		});
	};

}



