import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'joinStyle'
})
export class JoinStylePipe implements PipeTransform {

  transform(value: any, args?: any): any {
    let base = value;
    let addOn = args;
    if(base && addOn) {
      return Object.assign({}, base, addOn);
    } else if(base) {
      return base;
    } else {
      return null;
    }
  }

}



