import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'backgroundUrl'
})
export class BackgroundUrlPipe implements PipeTransform {

  transform(value: any, args?: any): any {
    let url: string = value;
    let style = {
      "background-image": "url('"+encodeURI(url)+"')"
    };
    return style;
  }

}



