import { Component, OnInit } from '@angular/core';
import * as L from '../../resources/language';
import { LayoutService } from '../../services/layout.service';

@Component({
	selector: 'app-works-page',
	templateUrl: './works-page.component.html',
	styleUrls: ['./works-page.component.scss']
})
export class WorksPageComponent implements OnInit {

	private language: any;

	private links: Array<{
		description: string,
		url: string,
		name: string,
		background: string
	}>;

	private parallax: {
		config: {
			initialValue: number;
			ratio: number;
		}
	};

	private page: {
		cols: number,
		colspan: number
	};

	constructor(private layoutService: LayoutService) {
		this.language = L.Language.en;

		this.links = [{
			description: this.language.commercial_desc,
			url: "commercial",
			name: this.language.commercial,
			background: "/assets/work_commercial.jpg"
		}, {
			description: this.language.residential_desc,
			url: "residential",
			name: this.language.residential,
			background: "/assets/work_residential.jpg"
		}, {
			description: this.language.hotel_desc,
			url: "hotel",
			name: this.language.hotel,
			background: "/assets/work_hotel.jpg"
		}, {
			description: this.language.office_desc,
			url: "office",
			name: this.language.office,
			background: "/assets/work_office.jpg"
		}];

		this.parallax = {
			config: {
				initialValue: 100,
				ratio: -0.6
			}
		};

		this.page = {
			cols: 2,
			colspan: 1
		};
	}

	ngOnInit() {
		this.drawLayout();
	}

	private drawLayout = function() {
		this.layoutService.dimension((width, height) => {
			// console.log(width, height);
			if(width < 750) {
				this.page.cols = 1;
				this.page.colspan = 1;
			} else {
				this.page.cols = 2;
				this.page.colspan = 1;
			}
		});
	};

}



